<template>
  <div>
        <div class="header">
            <el-button type="primary" size="mini">上传</el-button>
            <el-button icon="el-icon-full-screen" size="mini">全屏</el-button>
            <el-input placeholder="请输入内容" id="input"></el-input>
        </div>
        <!-- 显示数据 -->
        <el-table style="width: 100%" border :data="waterPumpData">
            <el-table-column label="系统" width="80" align="center">CW1</el-table-column>
            <el-table-column label="参数类型" width="width">---</el-table-column>
            <el-table-column prop="id" label="参数ID" width="width"></el-table-column>
            <el-table-column prop="name" label="参数名称" width="width"></el-table-column>
            <el-table-column label="操作" width="width">
                    <template slot-scope="{row}">
                        <el-button icon="el-icon-full-screen" size="mini" @click="getLineChartData(row.id,1,row.name)">数据可视化</el-button>
                    </template>
            </el-table-column>
        </el-table>
        <!-- 分页器 
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
        -->
        <el-pagination
            style="text-align:center;margin-top:15px"
            :current-page="currentPage"
            :page-sizes="[3,5,10]"
            :page-size="limit"
            :total="total"
            layout="prev, pager, next, jumper,->, sizes, total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange">
        </el-pagination>
        <!-- 下边折线图抽屉 -->
        <el-drawer
            size="50%"
            direction="btt"
            :title="lineChartTitle"
            :visible.sync="isShow">
            <div style="width:100%;height:100%">
                <LineCharts :xData="xData" :yData="yData"/>
            </div>
      </el-drawer>
  </div>
</template>

<script>
    import LineCharts from '@/components/LineCharts'

    export default {
        name:'DataTable',
        components:{
            LineCharts
        },
        data(){
            return {
                waterPumpData:[],
                isShow:false,
                xData:[],
                yData:[],
                lineChartTitle:'',
                currentPage:1,
                limit:5,
                total:0
            }
        },
        mounted(){
            this.getWaterPumpData()
        },
        methods:{
            async getWaterPumpData(){
                let {currentPage,limit} = this
                let result = await this.$API.reqGetData(1,currentPage,limit)
                if(result){
                    this.waterPumpData = result.list
                    this.total = result.total
                }else{
                    Promise.reject(new Error('faile'))
                }
            },
            async getLineChartData(id,part,name){
                let result = await this.$API.reqLineChartY(id,1)
                // if(result.code == 200){
                //     this.yData = result.data
                //     let resultX = await this.$API.reqLineChartX()
                //     if(resultX.code == 200){
                //         this.xData = resultX.data
                //         return 'ok'
                //     }else{
                //         Promise.reject(new Error('faile'))
                //     }
                // }else{
                //     Promise.reject(new Error('faile'))
                // }
                if(result){
                    this.yData = result
                    console.log(this.yData)
                    let resultX = await this.$API.reqLineChartX()
                    if(resultX){
                        this.xData = resultX
                        this.isShow = true
                        this.lineChartTitle = "系统名称:CW1"+"  参数ID:"+id+"  参数名称:"+name
                        return 'ok'
                    }else{
                        Promise.reject(new Error('faile'))
                    }
                }else{
                    Promise.reject(new Error('faile'))
                }

            },
            handleSizeChange(limit){
                this.limit = limit
                this.getWaterPumpData()
            },
            handleCurrentChange(page){
                this.currentPage = page
                this.getWaterPumpData()
            }
        },
    }
</script>

<style scope>
    .header{
        display: flex;
        margin-bottom: 10px;
    }
    #input{
        float:right;
        width: 150px;
    }
</style>