<template>
    <div ref="charts" class="charts">
    </div>
</template>

<script>
    import * as echarts from "echarts"

    export default {
        name:"LineCharts",
        props:{
            xData:[],
            yData:[]
        },
        mounted(){
            //初始化echarts实例
            let lineCharts = echarts.init(this.$refs.charts)
            //配置数据
            lineCharts.setOption({
                xAxis: {
                    type: 'category',
                    data: this.xData,
                },
                yAxis: {
                    type: 'value'
                },
                dataZoom: [
                    {
                        type: 'slider',
                        xAxisIndex: 0,
                        filterMode: 'empty'
                    },
                    {
                        type: 'inside',
                        xAxisIndex: 0,
                        filterMode: 'empty'
                    },
                ],
                series: [
                    {
                        data: this.yData,
                        type: 'line'
                    }
                ]
            })
        }
    }
</script>

<style>
    .charts{
        width: 100%;
        height: 100%;
    }
</style>