//对API进行统一管理
import requests from "./request"

export const reqLineChartX = () => requests({
    url:'/date',
    method:'get'
})

export const reqLineChartY = (id,part) => requests({
    url:`/getdig?id=${id}&part=${part}`,
    method:'get'
})

export const reqGetData = (group,pageNum,size) => requests({
    url:`/map?group=${group}&pageNum=${pageNum}&size=${size}`,
    method:'get'
})